// app/components/ProtectedRoute.tsx
'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useQuery } from '@apollo/client';
import { IS_LOGGED_IN } from '@/app/apollo/operations/auth';
import { User } from '@/app/apollo/client';

// Define the structure of the query response
interface IsLoggedInQuery {
  isLoggedIn: boolean;
  currentUser: User | null;
}

export default function ProtectedRoute({ 
  children, 
  allowedRoles = [],
  fallbackUrl = '/auth/signin'
}: { 
  children: React.ReactNode,
  allowedRoles?: string[],
  fallbackUrl?: string
}) {
  const router = useRouter();
  const { data, loading } = useQuery<IsLoggedInQuery>(IS_LOGGED_IN);

  useEffect(() => {
    if (!loading) {
      if (!data?.isLoggedIn) {
        router.push(fallbackUrl);
        return;
      }

      if (allowedRoles.length > 0 && data?.currentUser) {
        const userRole = data.currentUser.role.toLowerCase();
        if (!allowedRoles.includes(userRole)) {
          router.push('/unauthorized');
        }
      }
    }
  }, [loading, data, router, allowedRoles, fallbackUrl]);

  if (loading) {
    return <div className="flex h-screen items-center justify-center">Loading...</div>;
  }

  // Only render children if authenticated and role check passes
  if (!data?.isLoggedIn) return null;
  
  if (allowedRoles.length > 0 && data?.currentUser) {
    const userRole = data.currentUser.role.toLowerCase();
    if (!allowedRoles.includes(userRole)) return null;
  }

  return <>{children}</>;
}