// app/apollo/queries/events.js
import { gql } from '@apollo/client';

// Fragment for common event fields
const EVENT_FIELDS = gql`
  fragment EventFields on EventInterface {
    id
    name
    description
    category
    location
    startDate
    endDate
    tags
    isLikedByUser(userId: $userId)
    isSavedByUser(userId: $userId)
    ... on Event {
      status
      isPublic
      capacity
      creator {
        id
        username
        profilePicture
      }
      participantCount
    }
    ... on ExternalEvent {
      source
      externalUrl
      imageUrl
      coordinates {
        latitude
        longitude
      }
      likeCount
      saveCount
    }
  }
`;

// Query to get all events (both internal and external)
export const GetAllEvents = gql`
query GetAllEvents {
  allEvents(
    filter: {
      includeInternalEvents: true
      includeExternalEvents: true
    }
  ) {
    id
    name
    startDate
    location
    description
    coordinates {
      latitude
      longitude
    }
    ... on ExternalEvent {
      source
      location
      description
      coordinates {
        latitude
        longitude
      }
    }
  }
}`;

// Query to get events near a location
export const GET_EVENTS_NEAR_ME = gql`
  query GetEventsNearMe(
    $latitude: Float!
    $longitude: Float!
    $radiusInKm: Float
    $filter: AllEventsFilterInput
    $skip: Int
    $take: Int
    $userId: UUID!
  ) {
    eventsNearMe(
      latitude: $latitude
      longitude: $longitude
      radiusInKm: $radiusInKm
      filter: $filter
      skip: $skip
      take: $take
    ) {
      ...EventFields
    }
  }
  ${EVENT_FIELDS}
`;

// Query to get a single event by ID
export const GET_EVENT = gql`
  query GetEvent($id: ID!, $userId: UUID!) {
    event(id: $id) {
      ...EventFields
      participants {
        id
        rsvpStatus
        registrationStatus
        timestamp
        user {
          id
          username
          profilePicture
        }
      }
    }
  }
  ${EVENT_FIELDS}
`;

// Query to get an external event by ID
export const GET_EXTERNAL_EVENT = gql`
  query GetExternalEvent($id: ID!, $userId: UUID!) {
    externalEvent(id: $id) {
      ...EventFields
    }
  }
  ${EVENT_FIELDS}
`;

// Query to get events liked by a user
export const GET_USER_LIKED_EVENTS = gql`
  query GetUserLikedEvents($userId: UUID!) {
    userLikedEvents(userId: $userId) {
      ...EventFields
    }
  }
  ${EVENT_FIELDS}
`;

// Query to get events saved by a user
export const GET_USER_SAVED_EVENTS = gql`
  query GetUserSavedEvents($userId: UUID!) {
    userSavedEvents(userId: $userId) {
      ...EventFields
    }
  }
  ${EVENT_FIELDS}
`;

// Query to get events a user is participating in
export const GET_USER_PARTICIPATED_EVENTS = gql`
  query GetUserParticipatingEvents($userId: UUID!) {
    userParticipatedEvents(userId: $userId) {
      ...EventFields
      participants {
        id
        rsvpStatus
        registrationStatus
        timestamp
        user {
          id
          username
        }
      }
    }
  }
  ${EVENT_FIELDS}
`;